@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,700|Open+Sans+Condensed:300,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Open Sans Condensed, sans-serif;
    background: #FFF;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Open Sans Condensed, sans-serif;
}

button {
    cursor: pointer;
}

/*---------------------*/

.project-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
}

/*---------------------*/
.project-content .main {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.project-content img{
    width: 90%;
    padding: 20px;
    margin-top: 10px;
    margin-right: 20px;
    border-radius: 50px; 
 
}

.project-content .main .text {
    order: 2;
    width: 100%;
    max-width: 400px;
    padding: 20px;
    
}

.project-content .main h2 {
    font-size: 35px;
    font-weight: 500;
    color: rgba(0, 0, 0, 1);
    margin-top: 10px;
    margin-bottom: 35px;


}

.project-content .main p {
    font-size: 30px;
    color: rgba(0, 0, 0, 0.8);
}

/*---------------------*/
.project-content .secondary {
    padding: 10px;
    margin-top: -10px;   
}

.project-content .secondary img {
    width: 100%;
}

/*---------------------*/
@media(max-width:900px){
    .project-content img {
        display: block;
        margin: 0 auto;
        width: 80%;
        
    }
    
    .project-content .main {
        display: block;
        padding: 0;
    }
    
    .project-content .main .text{
        margin: 0px auto;
        max-width: 500px;
        padding-top: 0px;     
    }

    .project-content .main h2 {
        font-size: 30px;
        margin-top: 35px;
        margin-bottom: 15px;
    }

    .project-content .main p {
        font-size: 25px;
    }

    .project-content .secondary img{
        width: 83%;
    }
}

@media(max-width:600px){
    .project-content img {
        width: 90%;
    }

    .project-content .main .text {
        width: 85%;
        max-width: 430px;
    }

    .project-content .main h2 {
        font-size: 24px;
        margin-top: 35px;
        margin-bottom: 15px;
    }

    .project-content .main p {
        font-size: 20px;
    }

    .project-content .secondary img{
        width: 95%;
    }
}

.home-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
    
}


/*---------------------*/
.home-main .wrap {
    padding: 1.7em;
    -webkit-column-count: 3;
            column-count: 3;
}

.home-main .wrap img {
    width: 100%;
    padding: 20px 15px;
    
} 

.home-main .wrap img:hover {
    -webkit-filter:opacity(40%);
            filter:opacity(40%);
    transform: scale(1.035);
}

/*---------------------*/

@media(max-width:650px) {
    .home-main .wrap{
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media(max-width:500px) {
    .home-main .wrap{
        -webkit-column-count: 1;
                column-count: 1;
    }

    .home-main .wrap img {
        width: 75%;
        display: block;
        margin: 0 auto;
    }
}
.site-header{
    width: 100%;
    max-width: 400px;
    margin: 50px auto;
    font: 400 14px Libre Baskerville, sans-serif;
}

.site-header h1{
    
    text-align: center;
    font-weight: 500;
    font-size: 55px;
    color: rgba(0, 0, 0, 0.6);
    
}

.site-header .navigation ul{
    display: flex;
    align-items: center;
    justify-content: center;
}

.site-header .navigation li {
    list-style: none;
}

.back-link {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.6);
    transition: all 0.2s;
    letter-spacing: 4px;
    font-size: 24px;
}

.back-link:hover {
    -webkit-filter: brightness(60%);
            filter: brightness(60%);
    font-weight: 700;
    color: rgba(0, 0, 0, 1);

}

.circle {
    background-color: rgba(0, 0, 0, 0.4);
    width: 8.5px;
    height: 8.5px;
    border-radius: 100px;
    margin: 12px;
}

@media(max-width:600px){
    .site-header {
        margin: 25px auto 5px auto;
    }

    .site-header h1 {
        font-size: 40px;
    }

    .back-link {
        font-size: 15px;
        letter-spacing: 3.6px;
    }

    .circle {
        width: 6.8px;
        height: 6.8px;
        margin: 8px;
    }    
}
footer {
    margin-bottom: 50px;
    text-align: center;
}

footer img {
    width: 150px;
}

footer img:hover {
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
}

footer p {
    font-size: 20px;
}

@media(max-width:600px){
    footer img {
        width: 110px;
    }
    
    footer p {
        font-size: 15px;
    }
}
.about {
    text-align: center;
    padding-top: 1px;
    margin-top: -4px;
    margin-bottom: 48px;
}

.about .wrap {
    width: 380px;
    text-align: center;
    margin: 0px auto;
}

.about .wrap h3 {
    font-weight: 500;
    font-size: 30px;
    color:rgba(0, 0, 0, 0.75);
    margin-top: 35px;
}

.about .wrap p {
    font-size: 25px;
    color:rgba(0, 0, 0, 0.75);
}

@media(max-width:600px){
    .about .wrap {
        width: 320px;
    }

    .about .wrap h3 {
        font-size: 25px;
    }

    .about .wrap p {
        font-size: 20px;
    }
}    
.project-content .secondaryb {
    margin-left: -4px;
}

.project-content .secondaryb img {
    width: 54%;  
}


@media(max-width:900px){
    .project-content .secondaryb img{
        width: 80%;
    }
}

@media(max-width:600px){
    .project-content .secondaryb img{
        width: 90%;
        margin-left: 30px;
    }
}
